import React,{ useState, useEffect } from "react";
import { useParams } from 'react-router-dom'
import style from './text.module.css'
import ReactMarkdown  from 'react-markdown'

export default function MemoTXT(props){
  let {id} = useParams()
  let [content, setContent] = useState('loading...');
  useEffect(()=>{
    // console.log(id)
    fetch(`http://rukafan.club:5000/${id}.md`,{  mode: "cors"})
    .then(res=>res.text())
    .then(data=>{
      // console.log(data)
      setContent(<ReactMarkdown source={data}/>)
    })
  },[id])
  return (
    <div className={style.App}>
      {content}
    </div>
  )
}