import React, { useState } from 'react';
// import logo from './logo.svg';
import { useHistory, useLocation } from 'react-router-dom'
import style from './Header.module.css';

function Header() {
  let [cur_posi, setCur_posi] = useState('')
  let history = useHistory()
  let location = useLocation();
  React.useEffect(() => {
    // console.log(location.pathname)
    setCur_posi(location.pathname)
  }, [location.pathname]);

  function Home(props){
    let cur = props.cur
    let tar = '/'
    function goJS() {
      history.push('/')
    }
    if(cur === tar){
      return <div className={style.App_header_div} onClick={goJS}>EcmaScript</div>
    } else {
      return <div className={style.App_header_div_off} onClick={goJS}>EcmaScript</div>
    }
  }
  function Blockchain(props){
    let cur = props.cur
    let tar = '/Blockchain'
    function goBlockchain() {
      // console.log(history)
      history.push('/Blockchain')
    }
    if(cur === tar){
      return <section className={style.App_header_sections} onClick={goBlockchain}>Blockchain</section>
    } else {
      return <section className={style.App_header_sections_off} onClick={goBlockchain}>Blockchain</section>
    }
  }
  function Music(props){
    let cur = props.cur
    let tar = '/Music'
    function goMusic() {
      // console.log(history)
      history.push('/Music')
    }
    if(cur === tar){
      return <section className={style.App_header_sections} onClick={goMusic}>Music</section>
    } else {
      return <section className={style.App_header_sections_off} onClick={goMusic}>Music</section>
    }
  }
  function Memo(props){
    let cur = props.cur
    let tar = '/Memos'
    function goMemo() {
      // console.log(history)
      history.push('/Memos')
    }
    if(cur === tar){
      return <section className={style.App_header_sections} onClick={goMemo}>Memos</section>
    } else {
      return <section className={style.App_header_sections_off} onClick={goMemo}>Memos</section>
    }
  }
  function Colors(props){
    let cur = props.cur
    let tar = '/Colors'
    function goColors() {
      // console.log(history)
      history.push('/Colors')
    }
    if(cur === tar){
      return <section className={style.App_header_sections} onClick={goColors}>Colors</section>
    } else {
      return <section className={style.App_header_sections_off} onClick={goColors}>Colors</section>
    }
  }
  return (
    <header className={style.App_header}>
      <Home cur={cur_posi}></Home>
      <Blockchain cur={cur_posi}></Blockchain>
      <Music cur={cur_posi}></Music>
      <Memo cur={cur_posi}></Memo>
      <Colors cur={cur_posi}></Colors>
    </header>
  );
}

export default Header;
