import React from "react";
import {
  Switch,
  Route
} from "react-router-dom";

import style from './Router.module.css';

// import ReactJS from './Ecmascript/Ecmascript.js'
// import Colors from './Colors/Colors.js'
// import Music from './Music/Music.js'
// import Memo from './Memo/Memo.js'
// import Blockchain from './Blockchain/Blockchain.js'
import MemoTXT from './Memo/Text/MemoTXT.js'
import ESTXT from './Ecmascript/Text/ESTXT.js'
import BlockchainTXT from './Blockchain/Text/BlockchainTXT.js'
import NoMatch from './../utils/NoMatch.js'
import loader from './../utils/loader.js'
// import Memo from './Memo/Memo.js'
let Home = loader(()=>import('./Ecmascript/ES.js'))
let Colors = loader(()=>import('./Colors/Colors.js'))
let Music = loader(()=>import('./Music/Music.js'))
let Memo = loader(()=>import('./Memo/Memo.js'))
let Blockchain = loader(()=>import('./Blockchain/Blockchain.js'))
// import E1 from './Ecmascript/Ecmascript.js'


function Router () {
  return (
    <div className={style.App}>
      <Switch>
        <Route exact path='/'>
          <Home></Home>
        </Route>
        <Route path='/Blockchain'>
          <Blockchain></Blockchain>
        </Route>
        <Route exact path='/Music'>
          <Music></Music>
        </Route>
        <Route exact path='/Memos'>
          <Memo></Memo>
        </Route>
        <Route exact path='/Colors'>
          <Colors></Colors>
        </Route>
        <Route exact path='/AboutThisSite'>
          <Colors></Colors>
        </Route>
        <Route exact path='/AsyncComponents'>
          <Colors></Colors>
        </Route>
        <Route path="/Memos/:id" children={<MemoTXT />} />
        <Route path="/ES/:id" children={<ESTXT />} />
        {/* <Route path="/Blockchain/:id" children={<MemoTXT />} /> */}
        <Route path='*'>
          <NoMatch></NoMatch>
        </Route>
      </Switch>
    </div>
  )
}

export default Router;
